import { crew } from './../models/crew.model';
import { PipeTransform, Pipe } from '../../../node_modules/@angular/core';
@Pipe(
    {
        name : 'crewFilter'
    }
)
export class CrewFilterPipe implements PipeTransform
{
    transform(crew: crew[], searchterm: string):crew[]{
        if(!crew || !searchterm)
        {
            return crew;
        }
        return crew.filter(crew =>
            crew.name.toLowerCase().indexOf(searchterm.toLowerCase())!==-1);
    }
}